* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  background: var(--color-bg);
}

a {
  color: unset;
  text-decoration: none;
}

.header-section {
  height: 100vh;
}

.gradient-bg {
  /* ff 3.6+ */
  background: -moz-radial-gradient(circle at 3% 25%, rgba(236, 207, 160, 1) 44%, rgba(155, 90, 47, 1) 99%);

  /* safari 5.1+,chrome 10+ */
  background: -webkit-radial-gradient(circle at 3% 25%, rgba(236, 207, 160, 1) 44%, rgba(155, 90, 47, 1) 99%);

  /* opera 11.10+ */
  background: -o-radial-gradient(circle at 3% 25%, rgba(236, 207, 160, 1) 44%, rgba(155, 90, 47, 1) 99%);

  /* ie 10+ */
  background: -ms-radial-gradient(circle at 3% 25%, rgba(236, 207, 160, 1) 44%, rgba(155, 90, 47, 1) 99%);

  /* global 92%+ browsers support */
  background: radial-gradient(circle at 3% 25%, rgba(236, 207, 160, 1) 44%, rgba(155, 90, 47, 1) 99%);
}


.gradient-text {
  background: var(--gradient-text);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.gradient-text-dark {
  background: var(--gradient-text-dark);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.section-padding {
  padding: 4rem 6rem;
}

.section-margin {
  margin: 4rem 6rem;
}

.scale-up-center {
  -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes scale-up-center {
  0% {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

